import { Socket } from "socket.io-client"

enum SocketsActions {
    LOAD_GAME_SOCKET = 'LOAD_GAME_SOCKET',
    LOAD_USER_SOCKET = 'LOAD_USER_SOCKET',
    LOAD_MESSAGE_SOCKET = 'LOAD_MESSAGE_SOCKET'
}

interface LoadGameSocketAction {
    type: SocketsActions.LOAD_GAME_SOCKET,
    payload: Socket
}

interface LoadUserSocketAction {
    type: SocketsActions.LOAD_USER_SOCKET,
    payload: Socket
}

interface LoadMessageSocketAction {
    type: SocketsActions.LOAD_MESSAGE_SOCKET,
    payload: Socket
}

type SocketsAction = LoadGameSocketAction | LoadUserSocketAction | LoadMessageSocketAction
export type Sockets = { 'user'?: Socket, 'game'?: Socket, 'message'?: Socket }

const defaultSockets = { 'user': undefined, 'game': undefined, 'message': undefined }

export default (state: Sockets = defaultSockets, action: SocketsAction): Sockets => {
    switch (action.type) {
        case SocketsActions.LOAD_GAME_SOCKET: {
            return { ...state, 'game': action.payload }
        }
        case SocketsActions.LOAD_USER_SOCKET: {
            return { ...state, 'user': action.payload }
        }
        case SocketsActions.LOAD_MESSAGE_SOCKET: {
            return { ...state, 'message': action.payload }
        }
        default:
            return state
    }
}