import React, { useEffect, useMemo } from 'react';

// import Navbar from './components/Navbar'
// import ActiveGames from './components/ActiveGames'

import { ConnectionProvider, WalletProvider } from '@solana/wallet-adapter-react';
import { WalletAdapterNetwork } from '@solana/wallet-adapter-base';
import {
  getPhantomWallet,
  getSlopeWallet,
  getSolflareWallet,
} from '@solana/wallet-adapter-wallets';
import {
  WalletModalProvider,
  WalletMultiButton
} from '@solana/wallet-adapter-react-ui';
import { clusterApiUrl } from '@solana/web3.js';
import '@solana/wallet-adapter-react-ui/styles.css'
import { io } from "socket.io-client"
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import './css/wallet.css'
import Navbar from './components/Navbar';
import ActiveGames from './components/ActiveGames';
import Chat from './components/Chat';
import { useDispatch } from 'react-redux';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Profile from './components/Profile';
import Fairness from './components/Fairness';
import ScrollToTop from './ScrollToTop';

const network = WalletAdapterNetwork.Devnet

function App() {

  const endpoint = useMemo(() => clusterApiUrl(network), [network]);
  const wallets = useMemo(() => [
    getPhantomWallet(),
    getSlopeWallet(),
    getSolflareWallet()
  ], [network])
  const dispatch = useDispatch()

  useEffect(() => {
    const gameSocket = io("/game")
    const userSocket = io("/user")
    const messageSocket = io("/message")

    dispatch({ type: 'LOAD_GAME_SOCKET', payload: gameSocket })
    dispatch({ type: 'LOAD_USER_SOCKET', payload: userSocket })
    dispatch({ type: 'LOAD_MESSAGE_SOCKET', payload: messageSocket })
  }, [])

  return (
    <div className="App">
      <ConnectionProvider endpoint={endpoint}>
        <WalletProvider wallets={wallets} autoConnect>
          <WalletModalProvider>
            <BrowserRouter>
              <Navbar />
              <ScrollToTop />
              <div className='Body'>
                <Routes>
                  <Route path='/' element={<ActiveGames />} />
                  <Route path='/fairness' element={<Fairness />} />
                  <Route path='/u/:publicKey' element={<Profile />} />
                </Routes>
                <Chat />
              </div>
            </BrowserRouter>
          </WalletModalProvider>
          <ToastContainer autoClose={1500} />
        </WalletProvider>
      </ConnectionProvider>
    </div>
  );
}

export default App;
