import React, { useEffect, useMemo, useState } from 'react';

import { useWallet } from '@solana/wallet-adapter-react';
import axios from 'axios';

import { useDispatch, useSelector } from 'react-redux';

import { Connection, Keypair, LAMPORTS_PER_SOL, PublicKey, SystemProgram, Transaction } from '@solana/web3.js'
import { shortenAddress } from '../utils/shortenAddress';
import Countdown from 'react-countdown';
import Svg from 'react-inlinesvg'

import '../css/ActiveGames.css'
import '../css/Coin.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import { CombinedReducer } from '../store';
import Game from '../interfaces/Game';
import { createAvatar } from '@dicebear/avatars'
import * as avatarStyle from '@dicebear/adventurer';
import { toast } from 'react-toastify';
import { Sockets } from '../reducers/sockets';
import { Link } from 'react-router-dom';

import '../css/RecentGames.css'
import FairnessModal from './FairnessModal';

interface Props {
    games: Game[]
}

function RecentGames(props: Props) {
    const [gameForFairness, setGameForFairness] = useState<Game | null>()

    const closeFairnessModal = () => {
        setGameForFairness(null)
    }

    return (
        <>
            <table className='recentGames'>
                <thead>
                    <tr>
                        <th>Creator</th>
                        <th>Opponent</th>
                        <th>Bet</th>
                        <th>Result</th>
                        <th>Winner</th>
                        <th>Provably Fair</th>
                    </tr>
                </thead>
                <tbody>
                    {props.games.map(game =>
                        game.opponent && game.winner && game.result !== null ?
                            <tr key={game._id} className='recentGame'>
                                <td><div className="user tdContent"><Svg src={createAvatar(avatarStyle, { seed: game.creator.publicKey })} /> <Link to={`/u/${game.creator.publicKey}`}> {shortenAddress(game.creator.publicKey)}</Link></div></td>
                                <td><div className="user tdContent"><Svg src={createAvatar(avatarStyle, { seed: game.opponent.publicKey })} /> <Link to={`/u/${game.opponent.publicKey}`}> {shortenAddress(game.opponent.publicKey)}</Link></div></td>
                                <td><div className="bet tdContent">{game.amount / LAMPORTS_PER_SOL} <img src='./img/coins.png' /></div></td>
                                <td><div className="coin tdContent"><img src={`./img/coin${game.result}.png`} /></div></td>
                                <td><div className='user tdContent'><Svg src={createAvatar(avatarStyle, { seed: game.winner.publicKey })} /> <Link to={`/u/${game.winner.publicKey}`}> {shortenAddress(game.winner.publicKey)}</Link></div></td>
                                <td><button onClick={() => setGameForFairness(game)} className='openFairnessB'>Fairness</button></td>
                            </tr> : null)}
                </tbody>
            </table>
            {gameForFairness ? <FairnessModal game={gameForFairness} closeFairnessModal={closeFairnessModal} /> : null}
        </>
    );
}

export default RecentGames;