import React, { useEffect, useState } from 'react';

import { useWallet } from '@solana/wallet-adapter-react';
import axios from 'axios';

import { useDispatch, useSelector } from 'react-redux';

import '../css/ActiveGames.css'
import '../css/Coin.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import { CombinedReducer } from '../store';
import Game from '../interfaces/Game';
import { toast } from 'react-toastify';
import { Sockets } from '../reducers/sockets';
import GameBox from './GameBox';
import { LAMPORTS_PER_SOL } from '@solana/web3.js';

import { createAvatar } from '@dicebear/avatars'
import * as avatarStyle from '@dicebear/adventurer';
import Svg from 'react-inlinesvg'
import { Link } from 'react-router-dom';
import { shortenAddress } from '../utils/shortenAddress';
import RecentGames from './RecentGames';

const activeStatuses = ['active', 'joined', 'countdown', 'flipping']

function ActiveGames() {

    const wallet = useWallet()
    const [activeGames, setActiveGames] = useState<Game[]>([])
    const [recentGames, setRecentGames] = useState<Game[]>([])
    const [userGames, setUserGames] = useState<Game[]>([])
    const [openGames, setOpenGames] = useState<Game[]>([])
    const [createGameModalOpen, setCreateGameModalOpen] = useState(false)
    const [gameAmount, setgameAmount] = useState<number | null>(0)
    const [depositStatus, setDepositStatus] = useState('none')
    const [chosenCoin, setChosenCoin] = useState(0)

    const user = useSelector<CombinedReducer, User>((state) => state.user)
    const sockets = useSelector<CombinedReducer, Sockets>((state) => state.sockets)

    const inputgameAmount = (input: string) => {
        if (!input) return setgameAmount(null)
        console.log({ input })
        if (Number(input) >= 0) setgameAmount(Number(input) * LAMPORTS_PER_SOL)
        else setgameAmount(0)
    }

    const createGame = async () => {
        if (!(wallet && wallet.publicKey && gameAmount)) return

        if (user.balance < gameAmount) return toast.error('Balance needs to be higher than the game bet')

        try {
            await axios.post('/api/game', {
                amount: gameAmount,
                creatorChoice: chosenCoin
            })

            toast.success('Game created')
            setCreateGameModalOpen(false)
        } catch (e: any) {
            toast.error(e.response.data.message.toString())
        }
    }

    const gameUpdate = (updatedGame: Game) => {
        setActiveGames(old => old.map(game => {
            if (game._id === updatedGame._id) {
                if (updatedGame.status === 'ended') {
                    updatedGame.status = 'countdown'
                    updatedGame.countDownEnd = Date.now() + 3000
                }
                console.log(updatedGame)
                return updatedGame
            }
            return game
        }))
    }

    const removeGame = (gameToRemove: Game) => {

        setActiveGames(old => old.filter(game => game._id !== gameToRemove._id))
        setRecentGames(old => [gameToRemove, ...old.slice(0, 30)])
    }


    useEffect(() => {
        const getGamesData = async () => {
            const [activeGames, recentGames] = await Promise.all([
                axios.get('/api/game/allActive'),
                axios.get('/api/game/lastEnded')
            ])

            console.log({ activeGames: activeGames.data })
            setActiveGames(activeGames.data)
            setRecentGames(recentGames.data)
        }

        getGamesData()
    }, [])

    useEffect(() => {
        if (!sockets.game) return

        sockets.game.on('newGame', (game: Game) => setActiveGames(old => [...old, game]))
        sockets.game.on('gameUpdate', (updatedGame: Game) => gameUpdate(updatedGame))

    }, [sockets.game])

    useEffect(() => {
        let userGames: Game[] = []
        let openGames: Game[] = []
        activeGames.forEach(game => {
            if (game.status !== 'ended' && game.status !== 'cancelled') {
                game.creator._id === user?._id ? userGames.push(game) : openGames.push(game)
            }
        })
        setUserGames(userGames)
        setOpenGames(openGames)
    }, [user, activeGames])

    return (
        <div className='ActiveGames' >
            <div className="typeWrapper">
                <div className='type'>
                    <h2 className='typeHeader'>My Games</h2>
                    <h2 className='gamesCounter'>{userGames.length}</h2>
                </div>
                {user ? <button className='createGameB' onClick={() => setCreateGameModalOpen(true)}>Create New</button> : null}
            </div>
            {userGames.length > 0 ? <div className='games'>
                {userGames.map(game => {
                    return activeStatuses.includes(game.status) ? (
                        <GameBox key={game._id} game={game} removeGame={removeGame} />
                    ) : null
                })}
            </div> : <div className='typePlaceHolder'><p>Your CoinFlip games will appear here</p></div>}
            <div className='type'>
                <h2 className='typeHeader'>Open Games</h2>
                <h2 className='gamesCounter'>{openGames.length}</h2>
            </div>
            {openGames.length > 0 ? <div className='games'>
                {openGames.map(game => {
                    return activeStatuses.includes(game.status) ? (
                        <GameBox key={game._id} game={game} removeGame={removeGame} />
                    ) : null
                })}
            </div> : <div className='typePlaceHolder'><p>No open Coinflip games at the moment</p></div>}
            <div className='type'>
                <h2 className='typeHeader'>Recent Games</h2>
                <h2 className='gamesCounter'>{recentGames.length}</h2>
            </div>
            <RecentGames games={recentGames} />
            <div className='modal' style={{ display: createGameModalOpen ? 'flex' : 'none' }}>
                <div className='container'>
                    <div className='wrapper'>
                        <button className='closeB' onClick={() => setCreateGameModalOpen(false)}><svg width="14" height="14"><path d="M14 12.461 8.3 6.772l5.234-5.233L12.006 0 6.772 5.234 1.54 0 0 1.539l5.234 5.233L0 12.006l1.539 1.528L6.772 8.3l5.69 5.7L14 12.461z"></path></svg></button>
                        <div className='logo'>
                            <img alt='logo' src='./logo.png' />
                        </div>
                        <h1 className='title'>Create Game</h1>
                        {
                            depositStatus === 'none' ?
                                <>
                                    <div className='coinChooser'>
                                        <img onClick={() => setChosenCoin(0)} className={chosenCoin === 0 ? 'chosen' : ''} src='./img/coin0.png' />
                                        <img onClick={() => setChosenCoin(1)} className={chosenCoin === 1 ? 'chosen' : ''} src='./img/coin1.png' />
                                    </div>
                                    <input type="number" value={gameAmount === null ? '' : gameAmount / LAMPORTS_PER_SOL} onChange={(event) => inputgameAmount((event.target as HTMLInputElement).value)} />
                                    <button className='depositB' onClick={() => createGame()}>Create</button></>
                                :
                                depositStatus === 'sent' ?
                                    <>
                                        <h3 className='status'>Your deposit was sent <br />It will be validated within 60 seconds</h3>
                                    </> : <h3 className='status'>Failed</h3>
                        }
                    </div>
                </div>
                <div className='overlay'></div>
            </div>
        </div >
    );
}

export default ActiveGames;
