import React, { useEffect, useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';

import { useWallet } from '@solana/wallet-adapter-react';
import axios from 'axios';

import { useDispatch, useSelector } from 'react-redux';

import { Connection, Keypair, LAMPORTS_PER_SOL, PublicKey, SystemProgram, Transaction } from '@solana/web3.js'
import { shortenAddress } from '../utils/shortenAddress';
import Countdown from 'react-countdown';
import Svg from 'react-inlinesvg'

import '../css/Profile.css'
import '../css/Coin.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import { CombinedReducer } from '../store';
import Game from '../interfaces/Game';
import { createAvatar } from '@dicebear/avatars'
import * as avatarStyle from '@dicebear/adventurer';
import { toast } from 'react-toastify';
import { Sockets } from '../reducers/sockets';
import moment from 'moment'
import UserGames from './UserGames';

function Profile() {
    const { publicKey } = useParams()
    const [user, setUser] = useState<User>()
    const [games, setGames] = useState<Game[]>([])
    const [notFound, setNotFound] = useState(false)
    const [gamesWon, setGamesWon] = useState(0)
    const [gamesLost, setGamesLost] = useState(0)

    useEffect(() => {
        if (!publicKey) return

        const getUser = async (publicKey: string) => {
            const user: User = (await axios.get(`/api/u/${publicKey}`)).data
            const games: Game[] = (await axios.get(`/api/game/u/${publicKey}`)).data

            if (!user) setNotFound(true)

            setUser(user)
            setGames(games)
        }

        getUser(publicKey)
    }, [publicKey])

    useEffect(() => {
        if (!games || !user) return

        let gamesWon = 0
        let gamesLost = 0
        games.forEach(game => {
            if (game.status !== 'ended' || !game.winner) return

            if (game.winner._id === user._id) {
                gamesWon++
            } else {
                gamesLost++
            }
        })

        setGamesWon(gamesWon)
        setGamesLost(gamesLost)
    }, [user, games])

    return (
        <>
            {user ?
                <div className='Profile'>
                    <div className='profileBox'>
                        <Svg className='avatar' src={createAvatar(avatarStyle, { seed: user.publicKey.toString() })} />
                        <div className='info'>
                            <div className='address'>
                                <h1>{user.publicKey}</h1>
                                <a className='scanLink' target="_blank" rel="noopener noreferrer" href={`https://solscan.io/account/${user.publicKey.toString()}`}><img src='/img/solscan.png' /></a>
                            </div>
                            <div className='gameStats'>
                                <div className='gameStat'>
                                    <h2>Games Won:</h2>
                                    <h2>{gamesWon}</h2>
                                </div>
                                <div className='statCircle' style={{
                                    backgroundImage: games.length > 0 ? `linear-gradient(#041b37, #041b37), linear-gradient( 45deg, rgba(37, 185, 0, 0.85), rgba(37, 185, 0, 0.85) ${gamesWon / (gamesWon + gamesLost) * 100}%, rgba(185, 37, 0, 0.85) ${gamesLost / (gamesWon + gamesLost) * 100}% )` : `linear-gradient(#041b37, #041b37), linear-gradient( 45deg, gray, gray 100% )`
                                }}>
                                </div>
                                <div className='gameStat'>
                                    <h2>Games Lost:</h2>
                                    <h2>{gamesLost}</h2>
                                </div>
                            </div>
                        </div>
                    </div>
                    {games.length > 0
                        ? <UserGames user={user} games={games} />
                        :
                        null}
                </div>
                : null
            }
            {notFound ? <div className='Profile'><h1 className='notFound'>User was not found</h1> </div> : null}
        </>
    );
}

export default Profile;