import React, { useEffect, useMemo, useState } from 'react';
import Game from '../interfaces/Game';
import '../css/Modal.css'
import '../css/FairnessModal.css'
import { Link } from 'react-router-dom';

interface Props {
    game: Game,
    closeFairnessModal: () => void
}

function FairnessModal(props: Props) {

    return (
        <div className='modal' style={{ display: 'flex' }} >
            <div className='container'>
                <div className='wrapper'>
                    <button className='closeB' onClick={() => props.closeFairnessModal()}><svg width="14" height="14"><path d="M14 12.461 8.3 6.772l5.234-5.233L12.006 0 6.772 5.234 1.54 0 0 1.539l5.234 5.233L0 12.006l1.539 1.528L6.772 8.3l5.69 5.7L14 12.461z"></path></svg></button>
                    <div className='logo'>
                        <img alt='logo' src='/logo.png' />
                    </div>
                    <h1 className='title'>Fairness</h1>
                    <div className='fairness'>
                        <div className='field'>
                            <h4>Creator Public Key</h4>
                            <p>{props.game.creator.publicKey}</p>
                        </div>
                        <div className='field'>
                            <h4>Opponent Public Key</h4>
                            <p>{props.game.opponent?.publicKey}</p>
                        </div>
                        <div className='field'>
                            <h4>Private Seed</h4>
                            <p>{props.game.privateSeed}</p>
                        </div>
                        <div className='field'>
                            <h4>Private Seed Hash</h4>
                            <p>{props.game.privateSeedHash}</p>
                        </div>
                        <div className='field'>
                            <h4>Solana Blockhash</h4>
                            <p>{props.game.blockhash}</p>
                        </div>
                        <Link className='readMore' to='/fairness'>Read More</Link>
                    </div>
                </div>
            </div>
            <div className='overlay'></div>
        </div>
    );
}

export default FairnessModal;
