import React, { useEffect, useMemo, useState } from 'react';

import { useWallet } from '@solana/wallet-adapter-react';
import axios from 'axios';

import { useDispatch, useSelector } from 'react-redux';

import { Connection, Keypair, LAMPORTS_PER_SOL, PublicKey, SystemProgram, Transaction } from '@solana/web3.js'
import { shortenAddress } from '../utils/shortenAddress';
import Countdown from 'react-countdown';
import Svg from 'react-inlinesvg'

import '../css/ActiveGames.css'
import '../css/Coin.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import { CombinedReducer } from '../store';
import Game from '../interfaces/Game';
import { createAvatar } from '@dicebear/avatars'
import * as avatarStyle from '@dicebear/adventurer';
import { toast } from 'react-toastify';
import { Sockets } from '../reducers/sockets';
import { Link } from 'react-router-dom';

interface Props {
    game: Game,
    removeGame: (game: Game) => void
}

function GameBox(props: Props) {
    const [game, setGame] = useState<Game>()
    const [countDownEnd, setCountdownEnd] = useState<number>(0)

    const wallet = useWallet()
    const coinSpins = [
        ['spin1080', 'spin1440', 'spin1800', 'spin2160'],
        ['spin900', 'spin1260', 'spin1620', 'spin1980']
    ]

    const dispatch = useDispatch()
    const user = useSelector<CombinedReducer, User>((state) => state.user)

    useEffect(() => {
        setGame(props.game)
    }, [props.game])

    const getCoinAnimation = (game: Game): string => {
        if (game.result === null || game.result === undefined) throw 'Game is not loaded'

        const spinIndex = parseInt(game.privateSeedHash, 36) % coinSpins[game.result].length

        return coinSpins[game.result][spinIndex]
    }

    const changeGameStatus = (status: string) => {
        if (!game) return
        if (game.status === status) return

        if (game.winner?._id === user._id && status === 'revealed') dispatch({ type: 'UPDATE_USER_BALANCE', payload: game.amount * 2 })

        if (status === 'revealed') {
            setTimeout(() => {
                props.removeGame(game)
            }, 2000)
        }

        setGame({ ...game, status })
    }

    const joinGame = async () => {
        if (!(wallet && wallet.publicKey && game)) return

        if (user._id === game.creator._id) return toast.error('You can not join your game')
        if (user.balance < game.amount) return toast.error('Balance needs to be higher than the game bet')

        try {
            await axios.post('/api/game/join', {
                gameId: game._id
            })

            toast.success('Joined the game')
        } catch (e: any) {
            toast.error(e.response.data.message.toString())
        }
    }

    const cancelGame = async () => {
        if (!(wallet && wallet.publicKey && game)) return

        if (game.creator._id !== user._id) return toast.error('You can not cancel another person`s game')

        try {
            await axios.post('/api/game/cancel', {
                gameId: game._id
            })

            toast.success('Game has been cancelled')
        } catch (e: any) {
            toast.error(e.response.data.message.toString())
        }
    }

    return (
        <>
            {game && game.status !== 'ended' ? <div key={game._id} className='gameWrapper'>
                {game.creator._id === user?._id && game.status === 'active' ? <button className='closeB' onClick={() => cancelGame()}><svg width="14" height="14"><path d="M14 12.461 8.3 6.772l5.234-5.233L12.006 0 6.772 5.234 1.54 0 0 1.539l5.234 5.233L0 12.006l1.539 1.528L6.772 8.3l5.69 5.7L14 12.461z"></path></svg></button> : null}
                <div className="players">
                    <div className={game.status !== 'revealed' ? 'player' : game.winner?._id == game.creator._id ? 'player winner' : 'player loser'}>
                        <img src={`./img/coin${game.creatorChoice}.png`} className='choice' />
                        <Svg className='avatar' src={createAvatar(avatarStyle, { seed: game.creator.publicKey })} />
                        <Link to={`/u/${game.creator.publicKey}`} className='address'>{shortenAddress(game.creator.publicKey)}</Link>
                    </div>
                    <div className='middle'>
                        {game.status === 'active' || game.status === 'joined'
                            ? <h3 className='vsTag'>VS</h3>
                            : game.status === 'countdown' ?
                                <>
                                    <Countdown date={game.countDownEnd} onComplete={() => changeGameStatus('flipping')} renderer={time => <h3>{time.seconds}</h3>} /></>
                                :
                                <div onAnimationEnd={() => changeGameStatus('revealed')} id="coinFlip" className={getCoinAnimation(game)}>
                                    <div className="coin0"><img src='./img/coin0.png' /></div>
                                    <div className="coin1"><img src='./img/coin1.png' /></div>
                                </div>
                        }
                    </div>
                    <div className={game.status !== 'revealed' ? 'player' : game.winner?._id == game.opponent?._id ? 'player winner' : 'player loser'}>
                        {game.opponent ?
                            <>
                                <img src={`./img/coin${1 - game.creatorChoice}.png`} className='choice' />
                                <Svg className='avatar' src={createAvatar(avatarStyle, { seed: game.opponent.publicKey })} />
                                <Link to={`/u/${game.opponent.publicKey}`} className='address'>{shortenAddress(game.opponent.publicKey)}</Link>
                            </> :
                            <>
                                <Svg className='avatarPlaceholder' src='/img/playerPlaceholder.svg' />
                                <h3 className='address'>waiting...</h3>
                            </>}
                    </div>
                </div>
                <div className='info'>
                    <div className='bet'>
                        <h3>{game.amount / LAMPORTS_PER_SOL}</h3>
                        <img src='./img/coins.png' />
                    </div>

                    {!game.opponent && user ?
                        game.creator._id === user._id ?
                            <button className='deleteB' onClick={() => cancelGame()}><Svg src='/img/bin.svg' /></button>
                            :
                            <button onClick={() => joinGame()} className='joinB'>Join</button>
                        : <></>
                    }
                </div>
            </div> : null
            }
        </>
    );
}

export default GameBox;