import React, { useEffect, useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';

import { useWallet } from '@solana/wallet-adapter-react';
import axios from 'axios';

import { useDispatch, useSelector } from 'react-redux';

import { Connection, Keypair, LAMPORTS_PER_SOL, PublicKey, SystemProgram, Transaction } from '@solana/web3.js'
import { shortenAddress } from '../utils/shortenAddress';
import Countdown from 'react-countdown';
import Svg from 'react-inlinesvg'

import '../css/UserGames.css'
import '../css/Coin.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import { CombinedReducer } from '../store';
import Game from '../interfaces/Game';
import { createAvatar } from '@dicebear/avatars'
import * as avatarStyle from '@dicebear/adventurer';
import { toast } from 'react-toastify';
import { Sockets } from '../reducers/sockets';
import moment from 'moment'
import { Link } from 'react-router-dom';
import FairnessModal from './FairnessModal';

interface Props {
    games: Game[],
    user: User
}

function UserGames(props: Props) {
    const [gameForFairness, setGameForFairness] = useState<Game | null>()

    const closeFairnessModal = () => {
        setGameForFairness(null)
    }
    return (
        <>
            <table className='UserGames'>
                <thead>
                    <tr>
                        <th>Date</th>
                        <th>Bet</th>
                        <th>Picked Side</th>
                        <th>Opponent</th>
                        <th>Result</th>
                        <th>Win Amount</th>
                        <th>Provably Fair</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        props.games.map(game =>
                            <tr className='game'>
                                <td>{moment(game.createdAt).format('lll')}</td>
                                <td ><div className='tdContent bet'>{game.amount / LAMPORTS_PER_SOL} <img src='/img/coins.png' /></div></td>
                                <td><div className='tdContent coin'><img src={`/img/coin${game.creator._id === props.user._id ? game.creatorChoice : 1 - game.creatorChoice}.png`} /> </div></td>
                                <td>{game.creator._id === props.user._id ? (game.opponent ? <Link to={`/u/${game.opponent.publicKey}`}>{shortenAddress(game.opponent.publicKey)}</Link> : '-') : <Link to={`/u/${game.creator.publicKey}`}>{shortenAddress(game.creator.publicKey)}</Link>}</td>
                                <td><div className='tdContent coin'>{game.status === 'ended' ? <img src={`/img/coin${game.result}.png`} /> : game.status.charAt(0).toUpperCase() + game.status.slice(1)}</div></td>
                                <td>{game.status === 'ended' ? game.winner?._id === props.user._id ? <span className='win'>{Math.round(game.amount * 2 * ((100 - game.fee) / 100)) / LAMPORTS_PER_SOL}</span> : <span className='lose'>{-game.amount / LAMPORTS_PER_SOL}</span> : 0}</td>
                                <td><button onClick={() => setGameForFairness(game)} className='openFairnessB'>Fairness</button></td>
                            </tr>
                        )
                    }
                </tbody>
            </table>
            {gameForFairness ? <FairnessModal game={gameForFairness} closeFairnessModal={closeFairnessModal} /> : null}
        </>
    );
}

export default UserGames;