import React, { useEffect, useMemo, useState } from 'react';

import { useWallet, useAnchorWallet, WalletContextState } from '@solana/wallet-adapter-react';
import { io } from "socket.io-client"
import axios from 'axios';

import user from '../interfaces/User';
import { useDispatch, useSelector } from 'react-redux';

import { Connection, Keypair, LAMPORTS_PER_SOL, PublicKey, SystemProgram, Transaction } from '@solana/web3.js'
import { sendAndConfirmTransaction, clusterApiUrl, TokenAccountsFilter } from '@solana/web3.js'
import { Token, TOKEN_PROGRAM_ID, } from '@solana/spl-token'
import { WalletMultiButton } from '@solana/wallet-adapter-react-ui';
import { shortenAddress } from '../utils/shortenAddress';

import '../css/Navbar.css'
import '../css/Modal.css'
import User from '../interfaces/User';
import { CombinedReducer } from '../store';
import Svg from 'react-inlinesvg'

import { createAvatar } from '@dicebear/avatars'
import * as avatarStyle from '@dicebear/adventurer';
import { Sockets } from '../reducers/sockets';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import ClipLoader from "react-spinners/ClipLoader";

const messageToSign = Uint8Array.from(Buffer.from('I agree with Terms & Services of CookiMania.'))

const connection = new Connection(
    clusterApiUrl('devnet'),
    'confirmed',
);

const authPublicKey = new PublicKey('68a5pCnjnshTw3JXmht5j9rCJH9e9mzmguwQE8LighJX')

function Navbar() {

    const wallet = useWallet()
    const [isAskedToVerify, setIsAskedToVerify] = useState(false)
    const [isLoggedIn, setIsLoggedIn] = useState(false)
    const [depositModalOpen, setDepositModalOpen] = useState(false)
    const [depositAmount, setDepositAmount] = useState<number | null>(0)
    const [depositStatus, setDepositStatus] = useState('none')
    const [withdrawModalOpen, setWithdrawModalOpen] = useState(false)
    const [withdrawAmount, setWithdrawAmount] = useState<number | null>(0)
    const [withdrawnAmount, setWithdrawnAmount] = useState<number | null>(0)
    const [balanceChanged, setBalanceChanged] = useState('0')
    const [withdrawStatus, setWithdrawStatus] = useState('none')
    const [pendingAmount, setPendingAmount] = useState(0)
    const [prevBalance, setPrevBalance] = useState<number | null>(null)
    const dispatch = useDispatch()
    const user = useSelector<CombinedReducer, User>((state) => state.user)
    const associatedKeypair = useSelector<CombinedReducer, Keypair>((state) => state.associatedKeypair)
    const sockets = useSelector<CombinedReducer, Sockets>((state) => state.sockets)

    const connectUser = async (user: User) => {
        dispatch({ type: 'LOAD_WALLET', payload: wallet })
        dispatch({ type: 'LOAD_USER', payload: user })
        loadAssociatedKeypair()
        connectSocket(user)
        return setIsLoggedIn(true)
    }

    const loadAssociatedKeypair = async () => {
        const associatedKeypair = (await axios.get('/api/u/associatedKeypair', { withCredentials: true })).data
        const associatedKp = Keypair.fromSecretKey(Uint8Array.from(associatedKeypair.secretKey))
        dispatch({ type: 'LOAD_ASSOCIATED_KEYPAIR', payload: associatedKp })
    }

    const connectSocket = (user: User) => {
        if (!(sockets && sockets.user)) return

        sockets.user.emit('subscribeToProfile', user._id)
        sockets.user.on('balanceChange', (amount, fromDeposit) => {
            dispatch({ type: 'UPDATE_USER_BALANCE', payload: amount })
            console.log({ amount, fromDeposit, depositModalOpen })

            if (fromDeposit) setDepositStatus('validated')
        })
    }

    const inputDepositAmount = (input: string) => {
        if (!input) return setDepositAmount(null)
        console.log({ input })
        if (Number(input) >= 0) setDepositAmount(Number(input) * LAMPORTS_PER_SOL)
        else setDepositAmount(0)
    }
    const inputWithdrawAmount = (input: string) => {
        if (!input) return setWithdrawAmount(null)
        console.log({ input })
        if (Number(input) >= 0) setWithdrawAmount(Number(input) * LAMPORTS_PER_SOL)
        else setWithdrawAmount(0)
    }

    const deposit = async () => {
        if (!(wallet && wallet.publicKey && depositAmount)) return

        if (depositAmount < 0.01 * LAMPORTS_PER_SOL) return toast.error('Minimum deposit amount is 0.01')

        try {
            const transaction = new Transaction().add(
                SystemProgram.transfer({
                    fromPubkey: wallet.publicKey,
                    toPubkey: authPublicKey,
                    lamports: depositAmount
                })
            )

            await wallet.sendTransaction(transaction, connection)
            console.log({ transaction })
            setDepositStatus('sent')
        } catch (e: any) {
            toast.error(e.response.data.message.toString())
            setDepositStatus('failed')
        }
    }

    const withdraw = async () => {
        if (!(wallet && wallet.publicKey)) return

        setWithdrawStatus('withdrawing')

        const amount = await connection.getBalance(associatedKeypair.publicKey)
        const transaction = new Transaction().add(
            SystemProgram.transfer({
                fromPubkey: associatedKeypair.publicKey,
                toPubkey: new PublicKey(user.publicKey),
                lamports: amount
            })
        )
        transaction.feePayer = new PublicKey(user.publicKey)

        await wallet.sendTransaction(transaction, connection, { signers: [associatedKeypair] })

        setWithdrawnAmount(amount)
        setWithdrawStatus('withdrawn')
    }

    const requestWithdraw = async () => {
        if (!(wallet && wallet.publicKey && withdrawAmount)) return

        if (withdrawAmount < 0.01 * LAMPORTS_PER_SOL) return toast.error("Minimum withdraw is 0.01 SOLs")
        if (withdrawAmount > user.balance) return toast.error("Balance needs to be higher than the withdraw amount")

        try {
            setWithdrawStatus('withdrawing')
            setPendingAmount(withdrawAmount)
            await axios.post('/api/u/requestWithdraw', { amount: withdrawAmount })
            await new Promise((resolve) => setTimeout(() => resolve(1), 1000))
            withdraw()
        } catch (e: any) {
            toast.error(e.response.data.message.toString())
        }
    }

    const getAssociatedKeypairBalance = async () => {
        return connection.getBalance(associatedKeypair.publicKey)
    }

    const openWithdrawModel = async () => {
        const balanace = await getAssociatedKeypairBalance()
        if (balanace > 0.01) {
            setPendingAmount(balanace)
            setWithdrawStatus('hasPending')
        } else {
            setWithdrawStatus('none')
        }
        setWithdrawModalOpen(true)
    }

    useEffect(() => {
        const signAndAuth = async (wallet: WalletContextState) => {
            try {
                if (!wallet || !wallet.signMessage || !wallet.publicKey || isAskedToVerify) return
                setIsAskedToVerify(true)

                let { authenticated, user } = (await axios.get('/api/auth/state')).data
                console.log({ authenticated })
                if (authenticated) {
                    if (user.publicKey === wallet.publicKey.toBase58()) {
                        await connectUser(user)
                        return
                    }
                    console.log('PUBLIC KEYS ARE NOT THE SAME!')
                    await axios.get('/api/auth/logout')
                }

                const publicKey = wallet.publicKey.toBytes()
                const signedMessage = await wallet.signMessage(messageToSign)

                user = (await axios.post('/api/auth/login', { publicKey: Array.from(publicKey), signedMessage: Array.from(signedMessage) }, { withCredentials: true })).data

                await connectUser(user)
            } catch (e: any) {
                toast.error(e.response.data.message.toString())
                console.log(e)
            }
        }

        signAndAuth(wallet)
    }, [wallet])

    useEffect(() => {
        if (!user) return
        if (prevBalance != null) {
            console.log('Balance Update!', user.balance - prevBalance)
            if (user.balance - prevBalance >= 0) {
                setBalanceChanged('1')
            } else {
                setBalanceChanged('-1')
            }
        }
        setPrevBalance(user.balance)
    }, [user?.balance])

    return (
        <div className='Navbar' >
            <div className='left'>
                <Link to='/' className='logo'>
                    <img src='/logo.png' />
                    <h3 className='title'>solasphere</h3>
                </Link>
            </div>
            {
                isLoggedIn && wallet?.publicKey
                    ? (
                        <div className='user'>
                            <div className='balance'>
                                <img src='/img/coins.png' />
                                <h4 data-balancechanged={balanceChanged} onAnimationEnd={() => setBalanceChanged('0')} className='amount'>{Number((user?.balance / LAMPORTS_PER_SOL).toFixed(5))}</h4>
                            </div>
                            <button onClick={() => { setDepositModalOpen(true); setDepositStatus('none') }} className='depositB'>Deposit</button>
                            <button onClick={() => openWithdrawModel()} className='withdrawB'>Withdraw</button>
                            <Link to={`/u/${wallet.publicKey.toString()}`} className='profile'>
                                <Svg src={createAvatar(avatarStyle, { seed: wallet.publicKey.toString() })} />
                                <h4>{shortenAddress(wallet.publicKey.toBase58())}</h4>
                            </Link>
                        </div>
                    )
                    : (
                        <WalletMultiButton />
                    )
            }
            <div className='modal' style={{ display: depositModalOpen ? 'flex' : 'none' }}>
                <div className='container'>
                    <div className='wrapper'>
                        <button className='closeB' onClick={() => setDepositModalOpen(false)}><svg width="14" height="14"><path d="M14 12.461 8.3 6.772l5.234-5.233L12.006 0 6.772 5.234 1.54 0 0 1.539l5.234 5.233L0 12.006l1.539 1.528L6.772 8.3l5.69 5.7L14 12.461z"></path></svg></button>
                        <div className='logo'>
                            <img alt='logo' src='/logo.png' />
                        </div>
                        <h1 className='title'>Deposit</h1>
                        {
                            depositStatus === 'none' ?
                                <>
                                    <input type="number" value={depositAmount === null ? '' : depositAmount / LAMPORTS_PER_SOL} onChange={(event) => inputDepositAmount((event.target as HTMLInputElement).value)} />
                                    <button className='depositB' onClick={() => deposit()}>Deposit</button></>
                                :
                                depositStatus === 'sent' ?
                                    <>
                                        <h3 className='status'>Validating <ClipLoader size={13} color='#443f89' /></h3>
                                    </> : depositStatus === 'validated' ? <h3 className='status'>Succesfully validated</h3> : <h3 className='status'>Failed</h3>
                        }
                    </div>
                </div>
                <div className='overlay'></div>
            </div>
            <div className='modal' style={{ display: withdrawModalOpen ? 'flex' : 'none' }}>
                <div className='container'>
                    <div className='wrapper'>
                        <button className='closeB' onClick={() => setWithdrawModalOpen(false)}><svg width="14" height="14"><path d="M14 12.461 8.3 6.772l5.234-5.233L12.006 0 6.772 5.234 1.54 0 0 1.539l5.234 5.233L0 12.006l1.539 1.528L6.772 8.3l5.69 5.7L14 12.461z"></path></svg></button>
                        <div className='logo'>
                            <img alt='logo' src='/logo.png' />
                        </div>
                        <h1 className='title'>Withdraw</h1>
                        {
                            withdrawStatus === 'none'
                                ?
                                <>
                                    <input type="number" value={withdrawAmount === null ? '' : withdrawAmount / LAMPORTS_PER_SOL} onChange={(event) => inputWithdrawAmount((event.target as HTMLInputElement).value)} />
                                    <button className='depositB' onClick={() => requestWithdraw()}>Withdraw</button></>
                                :
                                withdrawStatus === 'hasPending' || withdrawStatus === 'withdrawing' ?
                                    <div className='pending'>
                                        <h2>Pending: {pendingAmount / LAMPORTS_PER_SOL} SOL</h2>
                                        <button className='depositB' disabled={withdrawStatus === 'withdrawing'} onClick={() => withdraw()}><span className='withdrawLabel'>Withdraw</span> {withdrawStatus === 'withdrawing' ? <ClipLoader size={13} color='#27244f' /> : null}</button>
                                    </div> : withdrawStatus === 'withdrawn' ?
                                        <h2>Succesfully withdrawn {withdrawnAmount ? withdrawnAmount / LAMPORTS_PER_SOL : null}</h2>
                                        : <h3 className='status'>Failed</h3>
                        }
                    </div>
                </div>
                <div className='overlay'></div>
            </div>
        </div >
    );
}

export default Navbar;
