import React, { useEffect, useMemo, useState } from 'react';
import '../css/Fairness.css';

function Fairness() {

    return (
        <div className='Fairness' >
            <h1>Fairness</h1>
            <p>Solasphere project is built on trust. We want everything to be clear and fair. We are here for a long-term game.</p>
            <h1>Result Algorithm</h1>
            <p>Our system generates the result for each round by using the SHA-256 hash of 4 separate inputs</p>
            <ol className='hashInputs'>
                <li>Game Creator Public Key</li>
                <li>Game Opponent Public Key</li>
                <li>Private Seed (cryptographically secure random number in hex format)</li>
                <li>Solana Head Block Hash</li>
            </ol>
            <p>
                Last 2 inputs plays the most important part. <br />
                <span className='term'>Private Seed</span> ensures that the game result will be truly random and it is generated with the game creation. We dont reveal it until the end, but you can check that it was not changed by hashing and comparing it with "privateSeedHash". "privateSeedHash" is always visible. <br />
                Using  <span className='term'>Solana Blockchash</span> solasphere proves that even we can't predict the result. Because this value can't be known in advance.
            </p>
            <h1>Check Fairness</h1>
            <p>You can execute Python code that checks fairness straight from your browser with <a target='_blank' rel="nofollow noopener" href='https://replit.com/@ApeTim/Solasphere-Coinflip-Fairness'>Fairness checker</a>. Just replace the inputs to correct ones. And you will see that the result will be the same. If you have any questions about this system, feel free to contact our support team.</p>
            <div className='fairnessCode'>
                <p>
                    from hashlib import sha256 <br />
                    <br />
                    coins = {'{0: "Blue Coin", 1: "Orange Coin"}'}<br />
                    <br />
                    creator_public_key = "INPUT"<br />
                    opponent_publick_key = "INPUT"<br />
                    private_seed = "INPUT"<br />
                    private_seed_hash = "INPUT"<br />
                    solana_block_hash = "INPUT"<br />
                    <br />
                    if sha256(private_seed.encode('utf-8')).hexdigest() != private_seed_hash: <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;print("Private Seed was changed")
                    <br />
                    else: <br />
                    &nbsp;&nbsp;&nbsp;&nbsp;seed = {'f"{creator_public_key}-{opponent_publick_key}-{private_seed}-{solana_block_hash}"'}<br />
                    &nbsp;&nbsp;&nbsp;&nbsp;resultHash = sha256(seed.encode('utf-8')).hexdigest()[:8]<br />
                    &nbsp;&nbsp;&nbsp;&nbsp;resultInt = int(resultHash, 16)<br />
                    &nbsp;&nbsp;&nbsp;&nbsp;result = resultInt % 2<br />
                    <br />
                    print(f"Game Result is: {'{coins[result]}"'})<br />
                </p>
            </div>
        </div>
    );
}

export default Fairness;
